@import url(https://fonts.googleapis.com/css?family=Bangers|Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Fredericka+the+Great&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    font-size: 1.2em;
    font-family: 'Lato', sans-serif;
    --black: #400101;
    --white: #F2EFDF;
    --red: #D90416;
    --redder: #BF1B39;
    --yellow: #F2B705;
}

html, body {
    margin:0;
    padding: 0;
    width: 100%;
    height: 100%;
    background: url(https://yea.edu.vn/wp-content/uploads/2016/01/tet-holiday-2016-slider.png) no-repeat;
    overflow: hidden;
}
.App_App__3gHKJ {
}

.App_title__2rNiT {
    font-family: 'Fredericka the Great', cursive;
    font-size: 3rem;
    text-align: center;
    margin:1rem auto 1rem auto;
    color:  #F2B705;
    color:  var(--yellow);
    width: 40%;
    text-shadow: 2px 2px 2px #BF1B39;
    text-shadow: 2px 2px 2px var(--redder);
    font-weight: bold;
}

.App_container__cIoKs {
    background-color: #fff;
    width: 60rem;
    height: 31rem;
    box-shadow: 10px 10px 10px #400101;
    box-shadow: 10px 10px 10px var(--black);
    border-radius: 10px;
    text-align: center;
    margin: 0 auto 0 auto;
    display: flex;
    flex-direction: column;

}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none; 
    margin:0;
}

.App_inputNumber__3oW1X {
    width: 2rem;
    -webkit-transform: translateX(1rem);
            transform: translateX(1rem);

}

.App_init__2tlTB {
    flex:9 1;
}

.App_init__2tlTB h3 {
    padding-top: 1rem;
    font-weight: bold;
    margin: 0 0 0.5rem 0;
    color: #F2B705;
    color: var(--yellow);
    flex:2 1;
    display: inline-block;
}

button {
    padding: 0.3rem;
    border-radius: 5px;
    border: 0;
    box-shadow: 2px 2px 2px black;
    cursor: pointer;
    color: white;
    font-weight: bold;
}

.App_startBtn__1ipXp {
    height: 3rem;
    width: 3rem;
    display: inline-block;
    -webkit-transform: scale(1.2) translateX(2rem);
            transform: scale(1.2) translateX(2rem);
    background-color:#D90416;
    background-color:var(--red);
    border-radius: 50%;
    margin: auto;
    z-index: 3;
    outline: none;
}

button:active {
    -webkit-transform: scale(1.2) translate(2rem,4px);
            transform: scale(1.2) translate(2rem,4px);
  }

.App_error__33SP6 {
    font-weight: bolder;
    color: #BF1B39;
    color: var(--redder);
    flex:0.5 1;
}

/* Game phase */
.App_game__3HkgB {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-auto-rows: minmax(200px, auto);
}

.App_game__3HkgB button {
    border-radius: 0;
    display: block;
    box-shadow: none;
    border: 1px solid black;
    font-size: 2rem;
}

.App_game__3HkgB button:active {
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
}

.App_game__3HkgB button:first-of-type {
    background-color: green;
}
.App_game__3HkgB button:last-of-type {
    background-color: red;

}

.App_error__33SP6 {
    font-size: 1.5rem;
    position: absolute;
    left:0;
    top:0;
    background-color: white;
    border-radius: 5px;
}

img.Player_icon__1aRye, img.Player_avatar__hJFjv {
    width: 60px;
    height: 60px;
}

img.Player_avatar__hJFjv {
    -webkit-transform: translateX(1.5rem);
            transform: translateX(1.5rem);
}

.Player_player__1s3kY {
    padding: 1rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    border: 1px solid #333;
    justify-content: center;
    align-items: center;
    grid-row-gap: 1rem;
    row-gap: 1rem;
}

.Player_active__3YZJr {
    background-color: blanchedalmond;
}

.Player_name__efXUr {
    font-weight: bold;  
    color: var(--redder);
    font-size: 1.5rem;
}

.Player_text__1_6_N {
    display: inline-block;
    color: var(--redder);
}

.Player_score__35xGd {
    color: var(--black);
    font-weight: bolder;
    font-size: 1.5rem;

}




.InitGame_container__34dP_ {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-row-gap: 2rem;
    row-gap: 2rem;
}
